<template>
    <div class="unfinish">
        <div class="un-text">
            <h1>正在进行</h1>
            <div class="un-number">{{getData.length}}</div>
        </div>
        <div class="list" v-for="(item) in getData" :key="item.id">
            
            <div class="list-input">
                <input type="checkbox" :checked="item.done" @change="change($event,item.id)">
            </div>
            <div class="list-text">{{item.title}}</div>
            <div class="list-icon" @click="del(item.id)">-</div>
            </div>
        
    </div>
</template>
<script>
export default {
    name:'unfinish',
    data() {
        return {
            num:0
        }
    },
    methods:{
        //更改数据状态
        change(el,a){
            var obj ={elme:el.target.checked,id:a}
            this.$store.commit('change',obj)
        },
        //删除
        del(a){
            this.$store.commit('del',a)
        }
    },
    computed:{
        getData(){
            return this.$store.getters.finishs;
        }
    }
}
</script>
<style lang="" scoped>

.un-text{
    margin: 20px 0;
    display: flex;
    justify-content: space-between;
}
.un-number{
    font-weight: bold;
    font-size: 14px;
    height: 20px;
    line-height: 22px;
    background: #E6E6FA;
    border-radius: 20px;
    padding: 0 5px;
    color: #666;
    margin-right: 3px;
    margin-top: 3px;
}
h1{
    font-size: 24px;
}
.list{
    height: 32px;
    line-height: 32px;
    background: #E6E6E6;
    border-radius: 3px;
    border-left: 5px solid #999;
    display: flex;
    margin-bottom: 10px;
}
.list-input{
    padding: 5px;
}
.list input{
    width: 22px;
    height: 22px;
    margin: 0px 3px 3px 3px;
    margin-left: 8px;
}
.list-text{
    width: 505px;
}
.list-icon{
    width: 14px;
    height: 12px;
    border: 6px double #E6E6E6;
    background: #CCC;
    line-height: 13px;
    text-align: center;
    border-radius:100% ;
    color: #fff;
    font-size: 14px;
    font-weight: bold;
    margin: 3px 5px 0px 15px;
    cursor: pointer;
}
</style>


