import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
var store = new Vuex.Store({
    state:{
        list:JSON.parse(localStorage.getItem('todo')) || [],
        id:JSON.parse(localStorage.getItem('id')) || 1
    },
    mutations:{
        //添加
        add(state,a){
            Vue.set(a,'id',state.id);
            state.list.push(a);
            state.id ++;
            var todos = JSON.stringify(state.list);
            localStorage.setItem('todo',todos);
            localStorage.setItem('id',state.id);
        },
        //改变状态
        change(state,a){
            state.list.forEach(el => {
                if (el.id == a.id) {
                    el.done = a.elme;
                    return;
                }
            });
            localStorage.setItem('todo',JSON.stringify(state.list))
        },
        //删除
        del(state,id){
            state.list.forEach((el,i,arr)=>{
                if (el.id == id) {
                    arr.splice(i,1);
                    return;
                }
            });
            localStorage.setItem('todo',JSON.stringify(state.list))
        }
        
    },
    getters:{
        unfinishs(state){
          var result = state.list.filter((el,i,arr)=>{
                return el.done == false;
            })
            console.log(result)
            return result;
        },
        finishs(state){
            var result = state.list.filter((el,i,arr)=>{
                return el.done == true;
            })
            console.log(result)
            return result;
        }
    }

})
export default store;


