<template>
  <div class="hello">
    <div class="hello-inpu">
   <div class="hello-in">
     <div>
     <h1>ToDoList</h1>
     </div>
     <div>
       <input type="text" v-model="msg" :title="msg.length>0?'':titles" placeholder="添加ToDo" @keyup.enter="adds">
     </div>
   </div>
   </div>
   <div class="content">
     <unfinish></unfinish>
     <finish></finish>
     <p>Copyright © 2014 todolist.cn clear</p>
   </div>
  </div>
</template>

<script>
import unfinish from '@/components/UnFinish'
import finish from '@/components/Finish'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg:'',
      titles:'请填写此字段'
    }
  },
  methods:{
    adds(){
      var obj = {title:this.msg,done:false}
      if (this.msg != '') {
        this.$store.commit('add',obj)
        this.msg = ''
      }
    }
  },
  components:{unfinish,finish}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  width: 100%;
}
.hello-inpu{
  width: 100%;
  background: rgba(47,47,47,0.98);
  
}
.hello-in{
  width: 600px;
  height: 50px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  background: rgba(47,47,47,0.98);
  color: white;
}
h1{
  line-height: 50px;
  font-weight: 400;
  font-size: 24px;
  color: #DDD;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;

}
input{
  width: 360px;
  height: 26px;
  border-radius: 5px;
  outline: none;
  text-indent: 10px;
  border: none;
  box-shadow: 0 1px 0 rgba(255,255,255,0.24), 0 1px 6px rgba(0,0,0,0.45) inset;
  margin-top: 12px;
}
.content{
  width: 600px;
  margin: 0 auto;

}
p{
  text-align: center;
  color: #666;
    font-size: 14px;
    text-align: center;
    margin-top: 20px;
}
</style>



